import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PutawayTransferOrdersComponent } from './putaway-transfer-orders/putaway-transfer-orders.component';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { AddPutawayTransferOrderComponent } from './putaway-transfer-orders/add-putaway-transfer-order/add-putaway-transfer-order.component';
import { PickawayTransferOrdersComponent } from './pickaway-transfer-orders/pickaway-transfer-orders.component';
import { BinTransferOrdersComponent } from './bin-transfer-orders/bin-transfer-orders.component';
import { AddBinTransferComponent } from './bin-transfer-orders/add-bin-transfer/add-bin-transfer.component';



const routes: Routes = [
  {
    path: '',
    component: PutawayTransferOrdersComponent
  },
  {
    path: 'add',
    component: AddPutawayTransferOrderComponent
  },
  {
    path: 'pickaway-list',
    component: PickawayTransferOrdersComponent
  },
  {
    path: 'bin-transfer-list',
    component: BinTransferOrdersComponent
  },
  {
    path: 'bin-transfer-list/add-bin-transfer',
    component: AddBinTransferComponent
  }
]

@NgModule({
  declarations: [PutawayTransferOrdersComponent, AddPutawayTransferOrderComponent, PickawayTransferOrdersComponent, BinTransferOrdersComponent, AddBinTransferComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    SearchModule,
    BtnAddModule
  ]
})
export class TransferOrderModule { }
